            "腾讯《一线》联系华大基因创始人之一、中国科学院北京基因组研究所研究员于军，他表示，这项专利授权来自MIT（麻省理工学院），“本身技术很简单，谁都能做。”但是南方科技大学抢先成为第一个吃螃蟹的人，还是很重要的，尤其是成功了。" +
            "于军说，“这是一个已经证明有效的位点。这个结果就是一种验证。”" +
            "五年前就有论文发表了，证明在实验室可以做到。" +
            "因为此属于世界首例基因编辑婴儿，并且出生于中国民营系的莆田医院，消息传出后，形成诸多槽点。" +
            "槽点一：为什么会是中国莆田系医院？" +
            "于军称，英国人相对宽松些，对于克隆人、人工授精等都有比较激进。但是类似实验在美国管得严，有法律风险。基因治疗是美国人干的，但是做得不好，致死过人，所以美国人现在很害怕，对此很严厉。" +
            "美国批准了7个类似的临床试验。" +
            "而在中国莆田医院做出来，“这里面有好多无奈。”" +
            "槽点二：有没有经过伦理委员会审批？" +
            "一个署名为Ent-evo的人在新浪微博评论：CRISPR基因编辑婴儿这个项目不是偷偷做的，之前是做了登记，也有了伦理委员会审批（虽然能找到的申请书只有一个深圳和美妇儿科医院的）。" +
            "这次编辑的CCR5基因，它有一个天然等位基因叫CCR5△32，少了32个碱基对。CCR5△32携带者确实对HIV-1有很强的对抗性，看起来生活也大致正常。现在HIV是一个很严重的问题，从人群上来说，有更多的CCR5△32应该利大于弊。" +
            "槽点三：究竟有没有副作用？" +
            "编辑的CCR5基因，删掉其中32个碱基，使其变成一个叫CCR5△32的基因，虽然CCR5△32抵抗HIV感染研究比较靠谱，但是，会不会使授权编辑的人患上其它疾病，目前难以预测。"
        }
      ]
    };
  },
  mounted() {
    if (this.add) {
      console.log(this.add);
      // console.log(this.add2);
      // this.news.unshift(this.add1);
      // this.news.unshift(this.add2);
      this.news = this.add;
    }
    if (this.ahh) {
      this.news = this.ahh;
    }
  },
  methods: {
    login() {
      this.$router.push({ name: "List" });
    },
    look(item) {
      console.log(item);
      this.$router.push({
        name: "Detail",
        params: { item: item, all: this.news }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  margin: -50px 150px 150px 150px;
}
.el-carousel__item h3 {
  color: #d6d9db;
  font-size: 14px;
  opacity: 0.75;
  line-height: 200px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #d6d9db;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d6d9db;
}
p {
  cursor: pointer;
  display: inline;
}
.three {
  bottom: 10px;
  position: absolute;
  font-size: 14px;
  color: #353333;
}
.two {
  font-size: 16px;
  margin-top: 5px;
  color: #908b8b;
}
.one {
  font-size: 18px;
  cursor: pointer;
}
.one:hover {
  color: rgb(8, 119, 223);
}

.news {
  border-bottom: #353333;
}
.news:hover {
  background-color: #ccccca;
}
.more {
  cursor: pointer;
}
.more:hover {
  color: rgb(15, 176, 240);
}
</style>



