<template>
    <div class="hello">
        <el-row>
            <el-col :span="2"><img style="margin-top:3px;width:70px;" src="../assets/logo2.png"></el-col>
            <el-col :span="4">
                <p style="float:left;font-size:18px;">
                    <b>{{ msg }}</b>
                </p>
            </el-col>
            <el-col :span="4" style="float:right;margin-top:20px;">
                <el-button type="text" @click="dialogVisible=true">登录</el-button>
                <el-button type="text">注册</el-button>
            </el-col>
        </el-row>
        <hr style="height:1px;border:none;border-top:2px solid rgb(171, 176, 179);" />
        <div style="height:8px;"></div>
        <el-row style="text-align:left;">
            <el-button type="text" style="margin-left:40px;" @click="back">返回</el-button>
        </el-row>
        <el-row>
            <h2>{{this.item.title}}</h2>
        </el-row>
        <el-row>
            <h4 style="color: gray;">{{this.item.abstract}}</h4>
            <h4 style="color: gray; font-size: 13px;">{{this.item.createtime}}</h4>
        </el-row>
        <hr style="height:1px;border:none;border-top:2px solid rgb(224, 227, 228);" />
        <el-row>
            <div style="text-align:left;margin:50px 100px 50px 100px;" id="content">
                <!-- {{item.content}} -->
            </div>
        </el-row>
        <el-dialog title="登录" :visible.sync="dialogVisible" width="40%">
            <el-row>
                <el-col :span="3" :offset="3">
                    <p style="margin-top:10px;">用户名：</p>
                </el-col>
                <el-col :span="12">
                    <el-input v-model="username" placeholder="请输入用户名"></el-input>
                </el-col>
            </el-row>
            <br />
            <el-row>
                <el-col :span="3" :offset="3">
                    <p>密码：</p>
                </el-col>
                <el-col :span="12">
                    <el-input v-model="password" type="password" placeholder="请输入密码"></el-input>
                </el-col>
            </el-row>
            <br/>
            <el-button type="primary" class="import-next" @click="dialogVisible = false,login()">登录</el-button>
        </el-dialog>
    </div>
</template>

<script>
export default {
  data() {
    return {
      msg: "长城投资信息平台",
      dialogVisible: false,
      username: "",
      password: "",
      item: this.$route.params.item,
      all: this.$route.params.all
    };
  },
  mounted() {
    console.log(this.item);
    console.log(this.all);
    $("#content").html(this.item.content);
  },
  methods: {
    login() {
      this.$router.push({ name: "List" });
    },
    back() {
      this.$router.push({ name: "Index", params: { ahh: this.all } });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  margin: -50px 150px 150px 150px;
}
</style>



