<template>
  <div>
    <router-link to="/">返回</router-link>
    <el-button type="primary" @click="shot">截图</el-button>
    <div>
      <a id="down1" class="down" href="" download="downImg">截图下载</a>
    </div>

    <el-row>
      <el-col :span="4">
        <div v-for="(item,index) in test" :key="item.id" class="tab" @click="choose(item)">{{item.name}}</div>
      </el-col>
      <el-col :span="20">
        <div class="container">
          <div class="draggable" v-for="(item,index) in test" @dblclick="double" :id="item.id" style="display:none;">
            <img :src="item.img">
            <div class="dragIcon" @mousedown="over($event,item)"></div>
            <div class="handle"></div>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
// import html2canvas from "html2canvas";
import axios from "axios";
import mockdata from "../../mock/mock.js";

export default {
  name: "Test",
  data() {
    return {
      test: [],
      disX: 0, //鼠标按下时光标的X值
      disY: 0, //鼠标按下时光标的Y值
      disW: 0, //拖拽前div的宽
      disH: 0, // 拖拽前div的高
      H: 0,
      W: 0
    };
  },
  created() {
    this.test = mockdata.data.message;
  },
  mounted() {
    var $draggable = $(".draggable").draggabilly({
      containment: ".container",
      handle: ".handle"
    });
  },
  methods: {
    choose(item) {
      console.log(item);
      $("#" + item.id).css("display", "");
    },
    shot() {
      // html2canvas(document.querySelector(".container")).then(canvas => {
      //   $("#down1").attr("href", canvas.toDataURL());
      //   //下载下来的图片名字
      //   $("#down1").attr("download", "KnowledgeGraph.png");
      // });
      html2canvas($(".container"), {
        onrendered: function(canvas) {
          $("#down1").attr("href", canvas.toDataURL());
          //下载下来的图片名字
          $("#down1").attr("download", "KnowledgeGraph.png");
        },
        useCORS: true
      });
    },

    over: function(ev, item) {
      console.log(item);
      var ev = ev || window.event;
      this.disX = ev.clientX; // 获取鼠标按下时光标x的值
      this.disY = ev.clientY; // 获取鼠标按下时光标Y的值
      this.disW = $("#" + item.id).width(); // 获取拖拽前div的宽
      this.disH = $("#" + item.id).height(); // 获取拖拽前div的高
      let that = this;
      document.onmousemove = function(ev) {
        var ev = ev || event;
        //拖拽时为了对宽和高 限制一下范围，定义两个变量
        that.W = ev.clientX - that.disX + that.disW;
        that.H = ev.clientY - that.disY + that.disH;
        if (that.W < 10) {
          that.W = 10;
        }
        if (that.H < 10) {
          that.H = 10;
        }
        $("#" + item.id).width(that.W); // 拖拽后物体的宽
        $("#" + item.id).height(that.H); // 拖拽后物体的高
      };
      document.onmouseup = function() {
        document.onmousemove = null;
        document.onmouseup = null;
      };
    },

    double: function() {
      alert("双击触发");
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.tab {
  cursor: pointer;
}
.draggable {
  width: 100px;
  height: 100px;
  /* background: #f90; */
  border-radius: 10px;
  /* cursor: pointer; */
  border: 1px solid black;
}
.draggable img {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
.draggable.is-pointer-down {
  background: rgb(255, 255, 255);
}
.draggable.is-dragging {
  opacity: 0.7;
}
.container {
  padding: 10px;
  border: 2px solid #f90;
  height: 600px;
}
.dragIcon {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 10%;
  height: 10%;
  opacity: 0;
  cursor: se-resize;
}
.handle {
  position: absolute;
  top: 0;
  left: 0;
  width: 90%;
  height: 90%;
  border-radius: 5px;
  cursor: move;
}
</style>



